.faq {
  padding-top: 130px;
}

.faq h2 {
  @include heading-style;
  margin-bottom: 60px;
  text-align: center;
}

.faq__accordeon {
  @include list-style;
  width: 71%;
  max-width: 970px;
  margin: 0 auto;
}

.faq__no-js .faq__item {
  padding: 50px 50px 38px 50px;
}

.faq__no-js h3 {
  display: block;
  margin: 0;
  padding: 0 0 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 23.4px;
}

.faq__no-js .faq__button {
  display: none;
}

.faq__no-js p {
  padding: 0;
}

.faq__item {
  padding: 0;
  border: 1px solid $light-brown;

  &:not(:last-of-type) {
    border-bottom: none;
  }
}

.faq__item h3 {
  display: none;
}

.faq__button {
  position: relative;
  width: 100%;
  height: 112px;
  padding: 50px 50px 38px 50px;
  border: none;
  background-color: transparent;
  font-family: $body-font-family;
  font-weight: 500;
  font-size: 18px;
  line-height: 23.4px;
  text-align: left;
  box-sizing: border-box;
  outline: none;
  transition: 0.4s;
  cursor: pointer;

  &::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 50px;
    transform: translateY(-50%);
    transform: rotate(180deg);
    width: 15px;
    height: 8px;
    background-image: url("../img/icon-angle.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.faq__item p {
  padding: -30px 50px 50px;
}

.active {
  &::after {
    transform: rotate(0deg);
  }
}

.hide {
  display: none;
}

.show {
  display: block;
}
