.blog {
  display: flex;
  flex-direction: column;
  padding-top: 130px;
}

.blog h2 {
  @include heading-style;
  margin-bottom: 35px;
  text-align: center;
}

.blog p {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 20.8px;
  text-align: center;

  & a {
    color: $basic-dark;
  }

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 64px;
    height: 1px;
    background-color: $light-brown;
  }
}

.blog__list {
  @include list-style;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 60px;
}

.blog__item {
  display: flex;
  width: fit-content;
  height: fit-content;

  &:nth-of-type(3n+1) {
    margin-bottom: 30px;
  }

  &--big {
    position: relative;
    margin: -630px 0 0 auto;
  }
}

.blog__card {
  width: 285px;
  min-height: 300px;
  padding: 60px 61px;
  text-align: center;
  background-color: #f0eae4;
  box-sizing: border-box;
}

.blog__item--big .blog__card {
  position: absolute;
  bottom: 0;
  right: 0;
}

.blog__card p {
  margin: 0 0 25px;
  font-weight: 500;
  font-size: 14px;
  line-height: 18.2px;
  color: $basic-grey;

  &::after {
    content: none;
  }
}

.blog__card h3 {
  @include heading-style;
  margin: 0 0 50px;
  font-weight: 600;
  font-size: 26px;
  line-height: 33.8px;
  color: $basic-black;
}

.blog__card a {
  display: block;
  position: relative;
  font-weight: 500;
  color: $basic-dark;

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 97px;
    height: 1px;
    background-color: $light-brown;
  }
}

@media (max-width: $tablet-breakpoint) {
  .blog {
    padding-top: 80px;
  }

  .blog__item {
    width: 100%;
    height: 300px;

    &--big {
      position: unset;
      margin: 0;
    }

    &:not(:last-of-type) {
      margin-bottom: 50px;
    }
  }

  .blog__item img {
    min-width: 339px;
    width: 466.5px;
    height: 300px;
  }

  .blog__card {
    min-width: 50%;
    padding: 71px 88px;
  }

  .blog__item--big .blog__card {
    position: unset;
  }
}

@media (max-width: $mobile-breakpoint) {
  .blog {
    padding-top: 70px;
  }

  .blog__list {
    margin-top: 50px;
  }

  .blog__item {
    flex-direction: column;
    height: unset;

    &:not(:last-of-type) {
      margin-bottom: 40px;
    }
  }

  .blog__item img {
    width: 737px;
    min-width: 290px;
    height: 256px;
  }

  .blog__card {
    width: 100%;
    min-height: 256px;
    padding: 49px 63px;
  }
}
